<template>
  <div id="agreement_details">
    <div>
      <div
        class="g_flex p10 m10"
        @click="goNext('/add-unit')"
      >
        <div class="g_left">{{ $route.query.cooperationName }}
          <div class="g_c_98">联系人：{{ $route.query.contact }} {{ $route.query.phone }}</div>
        </div>
        <div class="g_c_98">查看详情
          <van-icon
            name="arrow"
            class="g_middle"
          />
        </div>
      </div>
      <div
        class="g_flex p10"
        @click="goNext('/agreement-order')"
      >
        <div class="">查看账单</div>
        <div class="g_left g_c_98">待收款：￥{{ $route.query.unpayPrice }}
          <van-icon
            name="arrow"
            class="g_middle"
          />
        </div>
      </div>
    </div>
    <My-stateToday :propsData="refData" />
  </div>
</template>
<script>
import MyStateToday from '../State/StateToday.vue';
export default {
    components: { MyStateToday },
    data() {
        return {
            searchValue: null,
            refData: null
        };
    },
    watch: {},
    created() {
        this.$store.commit('setHeader', this.$route.query.cooperationName);
        this.refData = {
            accountUnitId: this.$route.query.accountUnitId,
            hotelId: this.$route.query.hotelId,
            cooperationName: this.$route.query.cooperationName,
            contact: this.$route.query.contact,
            phone: this.$route.query.phone
        };
    },
    methods: {
        //路径跳转
        goNext(link) {
            this.$router.push({
                path: link,
                query: {
                    hotelId: this.$route.query.hotelId,
                    accountUnitId: this.$route.query.accountUnitId,
                    cooperationName: this.$route.query.cooperationName
                }
            });
        }
    }
};
</script>

<style scoped>
#agreement_details {
    font-size: 14px;
}
.p10 {
    background: #fff;
    padding: 10px;
    line-height: 24px;
    margin-bottom: 10px;
}
.m10 {
    margin: 10px 0 2px 0;
}
</style>



