            v-if="ROLE_MANAGER == true && infoBox.status == 1"
            @click="lockRoom"
            class="boxBottom"
          >锁房</mt-button> -->
          <!-- <mt-button
            v-if="infoBox.status == 4 || infoBox.status == 5"
            @click="LiveRoom"
            class="boxBottom"
          >上线</mt-button> -->
          <!-- <mt-button
            @click="goCode"
            v-if="liveInfo.status==1 && liveInfo.order.orderAddSource == 3 && liveInfo.order.payment == 0 
                || liveInfo.status==2 && liveInfo.order.orderAddSource == 3 && liveInfo.order.payment == 0 
                || liveInfo.status==1 && liveInfo.order.orderAddSource == 3 && liveInfo.order.payment == 0 
                || liveInfo.status==2 && liveInfo.order.orderAddSource == 3 && liveInfo.order.payment == 0"
            class="boxBottom"
          >收款码</mt-button> -->
          <!-- <mt-button
            @click="btncancel(liveInfo)"
            v-if="liveInfo.roomSn && liveInfo.status == 1 && a < liveInfo.next_day && liveInfo.isCon == 0 && liveInfo.order.orderSource == '1' && liveInfo.conRoom != 1
                ||liveInfo.status == 1 && a < liveInfo.next_day && liveInfo.isCon != 0 && liveInfo.order.orderSource == '1' && liveInfo.conRoom != 1"
            class="boxBottom"
          >取消订单</mt-button>
          <mt-button
            @click="btncancel(liveInfo)"
            v-if="liveInfo.status == 1 && liveInfo.isCon == 0 && a < liveInfo.next_day && liveInfo.order.orderSource != '1' && liveInfo.conRoom != 1
                || liveInfo.status == 1 && liveInfo.isCon != 0 && a < liveInfo.next_day && liveInfo.order.orderSource != '1' && liveInfo.conRoom != 1
                || liveInfo.status == 1 && liveInfo.roomSn==null && a < liveInfo.next_day && liveInfo.order.orderSource != '1' && liveInfo.conRoom != 1"
            class="boxBottom"
          >取消订单</mt-button> -->
          <!-- <mt-button
            @click="checkOut"
            v-if="liveInfo.status == 1 || liveInfo.status == 2"
            class="boxBottom"
          >{{liveInfo.orderType!=3?'提前退房':'退租'}}</mt-button> -->
          <!-- <mt-button
            @click="btnlive(liveInfo)"
            v-if="liveInfo.status==1 && liveInfo.roomSn==null"
            class="boxBottom"
          >办理入住</mt-button> -->
          <!-- <mt-button
            v-if="infoBox.status == 3"
            @click="cleanRoom"
            class="boxBottom"
          >{{infoBox.isRoomCheck != 0?'打扫完成':'开始查房'}}</mt-button> -----------444-->
        </div>
      </div>
    </mt-popup>
    <!-- left -->
    <mt-popup
      v-model="show_left"
      position="bottom"
      style="width:100%;"
