          <van-icon name="arrow-down" />
        </div>
      </div>
      <div class="pd_flex p8">
        <div class="w30">保洁费用：</div>&emsp;
        <input
          class="w60"
          type="number"
          v-model="cleanAmount"
        />
      </div>
      <div class="pd_flex p8">
        <div class="w30">备注：</div>&emsp;
        <input
          class="w60"
          v-model="description"
        />
      </div>
      <div
        class="box bottom_pass"
        style="border-radius: 0 0 15px 15px;line-height:35px;"
      >
        <div
          class="pass_can"
          style="border-radius: 0 0 0 15px;"
          @click="cleanAmount = null, description = null, show_pai = false"
        >取消</div>
        <div
          class="pass_ok"
          style="border-radius: 0 0 15px 0;"
          @click="confirmDepart"
        >确定</div>
      </div>
    </mt-popup>
    <mt-popup
      v-model="show_paiList"
      position="bottom"
      style="width:100%;"
    >
      <van-picker
        title="保洁派单"
        show-toolbar
        :columns="cleaningList"
        @confirm="onConfirm"
        @cancel="show_paiList = false"
      />
    </mt-popup>
    <!-- 下发短信-修改密码 -->
    <mt-popup
      v-model="password_show"
      position="center"
      class="pass_box"
    >
      <p class="p_pass">请确认下发密码至该手机？</p>
      <div class="pass_show">
        <div>电话：</div>&emsp;
        <input v-model="pass_phone" />
      </div>
      <div class="box bottom_pass">
        <div
          class="pass_can"
          @click="pass_can"
        >取消</div>
        <div
          class="pass_ok"
          @click="pass_ok"
        >确定</div>
      </div>
    </mt-popup>
    <!-- 2.更多操作-展开的项-liveInfo-->
    <mt-popup
      v-model="more_box"
      v-if="infoBox.cooperId > 0"
    >111111111111111</mt-popup>
    <mt-popup
