        || more.status == 2 && more.orderType != 3
        || more.status == 2
        || more.roomSn && more.status == 1 && more.orderType == 3"
        @click="btnamend"
        type="primary"
        class="button_box"
      >换房</mt-button>
      <mt-button
        v-if="infoBox.status == 4 || infoBox.status == 5"
        @click="LiveRoom"
        type="primary"
        class="button_box"
      >上线</mt-button>
      <mt-button
        v-if="ROLE_MANAGER == true && infoBox.status == 1"
        @click="lockRoom"
        type="primary"
        class="button_box"
      >锁房</mt-button>
      <mt-button
        v-if="more.status == 1 && more.orderType == 3|| more.status == 2 && more.orderType == 3
        || more.status == 1 && more.orderType == 4|| more.status == 2 && more.orderType == 4"
        @click="changeWatch"
        type="primary"
        class="button_box"
      >换表</mt-button>
      <mt-button
        v-if="!more.roomSn && more.status == 1 && more.orderType != '3'"
        @click="btnchange(more)"
        type="primary"
        class="button_box"
      >换房型</mt-button>
      <mt-button
        @click="goCode"
        class="button_box"
        v-if="liveInfo.status==1 && liveInfo.order.orderAddSource == 3 && liveInfo.order.payment == 0 
        || liveInfo.status==2 && liveInfo.order.orderAddSource == 3 && liveInfo.order.payment == 0 
        || liveInfo.status==1 && liveInfo.order.orderAddSource == 3 && liveInfo.order.payment == 0 
        || liveInfo.status==2 && liveInfo.order.orderAddSource == 3 && liveInfo.order.payment == 0 "
      >收款码</mt-button>
      <mt-button
        v-if="more.status == 1 || more.status == 2"
        @click="checkOut(more)"
        type="primary"
        class="button_box"
      >{{ more.orderType != 3 ? '提前退房':'退租' }}</mt-button>
      <mt-button
