      <mt-button
        v-if="more.status == 1 && more.orderType == 3 && more.elecPayModel == 1|| more.status == 2 && more.orderType == 3 && more.elecPayModel == 1
        || more.status == 1 && more.orderType == 4 && more.elecPayModel == 1|| more.status == 2 && more.orderType == 4 && more.elecPayModel == 1"
        @click="eleCharge"
        type="primary"
        class="button_box"
      >电费充值</mt-button>
      <mt-button
        v-if='infoBox.status > 1'
        class="button_box"
        @click="goDetail"
      >查看订单详情</mt-button>
      <mt-button
        v-if="more.status == 1 && more.roomSn && more.password && more.order.payment>=5
        || more.status == 2 && more.roomSn && more.password && more.order.payment>=5"
        @click="btnGetInfo"
        type="primary"
        class="button_box"
      >重新下发短信</mt-button>
      <mt-button
        v-if="more.roomSn && more.status == 1 || more.roomSn && more.status == 2"
        @click="btnAgain"
        type="primary"
        class="button_box"
      >重新下发密码到锁</mt-button>
    </mt-popup>
  </div>
</template>
<script>
import { MessageBox, Toast } from 'mint-ui';
import { GetSystemDictDataByType } from '@/api/api_query';
import dayjs from 'dayjs';
import {
    PostBusinessRoomOnline,
    PostBusinessRoomCleanUp,
    PostBusinessRoomLock,
    GetBusinessRoombyhotel,
    GetBusinessOrderroomByroom,
    GetBusinessRoomscaleByhotel,
    GetBusinessOrderroom,
    PostBusinessOrderroomPasswordSend,
    PostBusinessOrderroomSendMessageAgain,
    PostBusinessOrderroomCancel,
    PostBusinessOrderroomLeave
} from '@/api/api_business_hotel_and_scale';
import { GetBusinessCooperOrderroomByroom } from '@/api/api_business';
import { PostBusinessCleaningorderAdd } from '@/api/api_staff';
import { GetBusinessOrderroomAcByAccountHotel, GetBusinessOrderroomByroombed } from '@/api/api_agreement';
export default {
    data() {
        return {
            new_list: [],
            rooms: [],
            top_rooms: [],
            show_info: false,
            rents: [],
            roomStatus: [],
            sorts: [],
            scales: [],
            bedInfoAll: [],
            dome: [],
            cleaningList: [],
            rent: '租赁类型',
            infoBox: '',
            liveInfo: '',
            currentRole: '',
            toDay: '',
            show_left: false,
            domeName: '',
            leftName: '全部酒店',
            show_center: false,
            center_1: '租赁类型',
            show_center_2: false,
            center_2: '房间状态',
            show_right: false,
            right: '所有房型',
            a: '',
            more_box: false,
            more: '',
            password_show: false,
            ROLE_MANAGER: false,
            ROLE_HOTEL_OWNER: false,
            show_pai: false, // 保洁派单
            show_paiList: false, // 保洁派单-列表
            pass_phone: '',
            login_info: null,
            hotelType: null,
            cleanType: '',
            cleanType_label: null,
            cleanAmount: null,
            description: null,
            unixCurrent: null,
            unixTime: null
        };
    },
    created() {
        this.$store.commit('setHeader', '实时房态');
        this.hotelId = this.$route.query.hotelId;
        let login_ok_info = JSON.parse(localStorage.getItem('current_user'));
        if (login_ok_info.id == '1') {
            this.ROLE_MANAGER = true;
        } else {
            this.roles = JSON.parse(localStorage.getItem('roles'));
            this.roles.some((item) => {
                if (item.name == 'ROLE_MANAGER') {
                    return (this.ROLE_MANAGER = true);
                } else {
                    return (this.ROLE_MANAGER = false);
                }
            });
            if (this.roles[0].name == 'ROLE_HOTEL_OWNER') {
                this.ROLE_HOTEL_OWNER = true;
            }
        }
        this.hotelType = localStorage.getItem('c_hotelType');
        this.param = {
            hotelId: this.hotelId
        };
        this.topList();
        this.getTop();
        this.toDay = dayjs().format('YYYY-MM-DD');
        this.a = dayjs().format('YYYY-MM-DD HH:mm:ss');
        this.unixCurrent = dayjs(this.a).unix();
    },
    computed: {
        computerWidth() {
            return (window.innerWidth - 40) / 3 + 'px';
        }
    },
    methods: {
        getTop() {
            GetSystemDictDataByType({ dictType: 'business_order_type' }).done((res) => {
                this.rents = res.data;
            });
            GetSystemDictDataByType({ dictType: 'business_room_status' }).done((res) => {
                this.roomStatus = res.data;
            });
            GetBusinessRoomscaleByhotel(this.hotelId).done((res) => {
                this.scales = res.data;
            });
            GetSystemDictDataByType({ dictType: 'business_cleaning_type' }).done((res) => {
                this.cleaningList = [];
                this.cleaningList_old = res.data.filter((item) => item.cssClass != 'hide');
                res.data.map((item) => {
                    if (item.cssClass != 'hide') {
                        this.cleaningList.push(item.dictLabel);
                    }
                });
            });
        },
        // 酒店全部房间
        topList() {
            if (this.$route.query.accountUnitId) {
                let data = {
                    hotelId: this.$route.query.hotelId,
                    accountUnitId: this.$route.query.accountUnitId
                };
                GetBusinessOrderroomAcByAccountHotel(data).done((result) => {
                    let oldDataArray = result.data.sort(this.objectArraySort('roomSn')).reverse();
                    //循环数组，先buildNum，floor分组
                    var buildNumArray = [];
                    var floorKey = [];
                    //下标
                    var buildNumArrayIndex = 0;
                    var floorKeyIndex = 0;
                    for (var i = 0; i < oldDataArray.length; i++) {
                        if (oldDataArray[i].endTime) {
                            oldDataArray[i].isSameTime = dayjs(this.toDay).isSame(dayjs(oldDataArray[i].endTime.substring(0, 10)));
                        }
                        if (buildNumArray.indexOf(oldDataArray[i].buildNum) == -1) {
                            buildNumArray[buildNumArrayIndex] = oldDataArray[i].buildNum;
                            buildNumArrayIndex++;
                        }
                        if (floorKey.indexOf(oldDataArray[i].floor) == -1) {
                            floorKey[floorKeyIndex] = oldDataArray[i].floor;
                            floorKeyIndex++;
                        }
                    }
                    var buildNumData = [];
                    for (var i = 0; i < buildNumArray.length; i++) {
                        var tempData = [];
                        var temoIndex = 0;
                        for (var j = 0; j < oldDataArray.length; j++) {
                            if (buildNumArray[i] == oldDataArray[j].buildNum) {
                                tempData[temoIndex] = oldDataArray[j];
                                temoIndex++;
                            }
                        }
                        var buildNumChilrenData = [];
                        var index = 0;
                        for (var h = 0; h < floorKey.length; h++) {
                            var floorChilren = [];
                            var floorChilrenIndex = 0;
                            for (var j = 0; j < tempData.length; j++) {
                                if (tempData[j].floor == floorKey[h]) {
                                    floorChilren[floorChilrenIndex] = tempData[j];
                                    floorChilrenIndex++;
                                }
                            }
                            //存在才添加子集，否则不添加，防止数组出现empty元素
                            if (floorChilren.length > 0) {
                                var floorObj = { floor: floorKey[h], children: floorChilren };
                                buildNumChilrenData[index] = floorObj;
                                index++;
                            }
                        }
                        buildNumChilrenData = buildNumChilrenData.sort(this.objectArraySort('floor'));
                        buildNumChilrenData.reverse();
                        if (buildNumChilrenData.length > 0) {
                            var buildNumObj = {
                                buildNum: buildNumArray[i],
                                sum: tempData.length,
                                children: buildNumChilrenData
                            };
                            buildNumData[i] = buildNumObj;
                        }
                    }
                    buildNumData.sort(this.objectArraySort('buildNum'));
                    this.rooms = buildNumData.reverse();
                    this.top_rooms = buildNumData;
                    this.top_rooms = buildNumData;
                });
            } else {
                GetBusinessRoombyhotel(this.param).done((res) => {
                    let oldDataArray = res.data.sort(this.objectArraySort('roomSn')).reverse();
                    //循环数组，先buildNum，floor分组
                    var buildNumArray = [];
                    var floorKey = [];
                    //下标
                    var buildNumArrayIndex = 0;
                    var floorKeyIndex = 0;
                    for (var i = 0; i < oldDataArray.length; i++) {
                        if (oldDataArray[i].endTime) {
                            oldDataArray[i].isSameTime = dayjs(this.toDay).isSame(dayjs(oldDataArray[i].endTime.substring(0, 10)));
                        }
                        this.isBeforeTime = dayjs(oldDataArray[i].endTime).isBefore(dayjs());
                        if ((oldDataArray[i].status == '2' && oldDataArray[i].roomType == '3' && this.isBeforeTime == true) || (oldDataArray[i].status == '2' && oldDataArray[i].roomType == '4' && this.isBeforeTime == true)) {
                            oldDataArray[i].show_dao = true;
                        } else {
                            oldDataArray[i].show_dao = false;
                        }
                        if (buildNumArray.indexOf(oldDataArray[i].buildNum) == -1) {
                            buildNumArray[buildNumArrayIndex] = oldDataArray[i].buildNum;
                            buildNumArrayIndex++;
                        }
                        if (floorKey.indexOf(oldDataArray[i].floor) == -1) {
                            floorKey[floorKeyIndex] = oldDataArray[i].floor;
                            floorKeyIndex++;
                        }
                    }
                    let buildNumData = [];
                    for (var i = 0; i < buildNumArray.length; i++) {
                        var tempData = [];
                        var temoIndex = 0;
                        for (var j = 0; j < oldDataArray.length; j++) {
                            if (buildNumArray[i] == oldDataArray[j].buildNum) {
                                tempData[temoIndex] = oldDataArray[j];
                                temoIndex++;
                            }
                        }
                        var buildNumChilrenData = [];
                        var index = 0;
                        for (var h = 0; h < floorKey.length; h++) {
                            var floorChilren = [];
                            var floorChilrenIndex = 0;
                            for (var j = 0; j < tempData.length; j++) {
                                if (tempData[j].floor == floorKey[h]) {
                                    floorChilren[floorChilrenIndex] = tempData[j];
                                    floorChilrenIndex++;
                                }
                            }
                            //存在才添加子集，否则不添加，防止数组出现empty元素
                            if (floorChilren.length > 0) {
                                var floorObj = { floor: floorKey[h], children: floorChilren };
                                buildNumChilrenData[index] = floorObj;
                                index++;
                            }
                        }
                        buildNumChilrenData = buildNumChilrenData.sort(this.objectArraySort('floor'));
                        buildNumChilrenData.reverse();
                        if (buildNumChilrenData.length > 0) {
                            var buildNumObj = {
                                buildNum: buildNumArray[i],
                                sum: tempData.length,
                                children: buildNumChilrenData
                            };
                            buildNumData[i] = buildNumObj;
                        }
                    }
                    buildNumData = buildNumData.sort(this.objectArraySort('buildNum'));
                    this.rooms = buildNumData.reverse();
                    this.top_rooms = buildNumData;
                });
            }
        },
        // 房间收支
        goNext() {
            this.$router.push({
                path: '/room-out',
                query: {
                    roomId: this.infoBox.id,
                    hotelId: this.$route.query.hotelId
                }
            });
        },
        // 保洁派单
        goPai() {
            this.show_info = false;
            if (this.cleaningList.length > 0) {
                this.cleanAmount = null;
                this.cleanType_label = this.cleaningList_old[0].dictLabel;
                this.cleanType = this.cleaningList_old[0].dictValue;
                this.show_pai = true;
            }
        },
        // 保洁派单
        onConfirm(value) {
            this.cleanType_label = value;
            this.cleanType = this.cleaningList_old.find((item) => item.dictLabel == value).dictValue;
            this.show_paiList = false;
        },
        confirmDepart() {
            if (!this.cleanAmount) {
                Toast('请填写保洁费用');
                return false;
            }
            let data = {
                orderRoomId: this.liveInfo.id || 0,
                roomId: this.infoBox.id,
                cleanType: this.cleanType,
                cleanAmount: this.cleanAmount,
                description: this.description
            };
            PostBusinessCleaningorderAdd(data).done((res) => {
                Toast(res.msg);
                this.show_pai = false;
                this.cleanType = null;
                this.cleanAmount = null;
                this.description = null;
            });
        },
        // 换表
        changeWatch() {
            this.$router.push({
                path: '/changeWatch',
                query: {
                    id: this.more.id,
                    orderId: this.more.orderId
                }
            });
        },
        // 电费充值
        eleCharge() {
            this.$router.push({
                path: '/eleManage',
                query: {
                    hotelName: this.more.hotel.hotelName,
                    scaleName: this.more.roomScale.scaleName,
                    virscaleName: this.more.virtualRoomScale.scaleName,
                    roomSn: this.more.roomSn,
                    orderRoomId: this.more.id,
                    orderId: this.more.orderId
                }
            });
        },
        // 查询酒店房间
        findList() {
            GetBusinessRoombyhotel(this.param).done((res) => {
                if (this.scaleName != '所有房型' && this.scaleName != undefined) {
                    res.data = res.data.filter((item) => item.roomScale.scaleName == this.scaleName);
                }
                let oldDataArray = res.data.sort(this.objectArraySort('roomSn')).reverse();
                //循环数组，先buildNum，floor分组
                var buildNumArray = [];
                var floorKey = [];
                //下标
                var buildNumArrayIndex = 0;
                var floorKeyIndex = 0;
                for (var i = 0; i < oldDataArray.length; i++) {
                    if (oldDataArray[i].endTime) {
                        oldDataArray[i].isSameTime = dayjs(this.toDay).isSame(dayjs(oldDataArray[i].endTime.substring(0, 10)));
                    }
                    this.isBeforeTime = dayjs(oldDataArray[i].endTime).isBefore(dayjs());
                    if ((oldDataArray[i].status == '2' && oldDataArray[i].roomType == '3' && this.isBeforeTime == true) || (oldDataArray[i].status == '2' && oldDataArray[i].roomType == '4' && this.isBeforeTime == true)) {
                        oldDataArray[i].show_dao = true;
                    } else {
                        oldDataArray[i].show_dao = false;
                    }
                    if (buildNumArray.indexOf(oldDataArray[i].buildNum) == -1) {
                        buildNumArray[buildNumArrayIndex] = oldDataArray[i].buildNum;
                        buildNumArrayIndex++;
                    }
                    if (floorKey.indexOf(oldDataArray[i].floor) == -1) {
                        floorKey[floorKeyIndex] = oldDataArray[i].floor;
                        floorKeyIndex++;
                    }
                }
                let buildNumData = [];
                for (var i = 0; i < buildNumArray.length; i++) {
                    var tempData = [];
                    var temoIndex = 0;
                    for (var j = 0; j < oldDataArray.length; j++) {
                        if (buildNumArray[i] == oldDataArray[j].buildNum) {
                            tempData[temoIndex] = oldDataArray[j];
                            temoIndex++;
                        }
                    }
                    var buildNumChilrenData = [];
                    var index = 0;
                    for (var h = 0; h < floorKey.length; h++) {
                        var floorChilren = [];
                        var floorChilrenIndex = 0;
                        for (var j = 0; j < tempData.length; j++) {
                            if (tempData[j].floor == floorKey[h]) {
                                floorChilren[floorChilrenIndex] = tempData[j];
                                floorChilrenIndex++;
                            }
                        }
                        //存在才添加子集，否则不添加，防止数组出现empty元素
                        if (floorChilren.length > 0) {
                            var floorObj = { floor: floorKey[h], children: floorChilren };
                            buildNumChilrenData[index] = floorObj;
                            index++;
                        }
                    }
                    buildNumChilrenData = buildNumChilrenData.sort(this.objectArraySort('floor'));
                    buildNumChilrenData.reverse();
                    if (buildNumChilrenData.length > 0) {
                        var buildNumObj = {
                            buildNum: buildNumArray[i],
                            sum: tempData.length,
                            children: buildNumChilrenData
                        };
                        buildNumData[i] = buildNumObj;
                    }
                }
                buildNumData = buildNumData.sort(this.objectArraySort('buildNum'));
                this.rooms = buildNumData.reverse();
            });
        },
        // 排序
        objectArraySort(keyName) {
            return function (objectN, objectM) {
                var valueN = objectN[keyName];
                var valueM = objectM[keyName];
                if (valueN < valueM) return 1;
                else if (valueN > valueM) return -1;
                else return 0;
            };
        },
        // 弹出框
        btnInfo(room) {
            this.infoBox = room;
            if (this.hotelType == '4') {
                this.getBedInfo(this.$route.query.accountUnitId ? this.infoBox.roomId : this.infoBox.id);
            } else {
                if (room.cooperId > 0) {
                    this.getCooperInfo(this.$route.query.accountUnitId ? this.infoBox.roomId : this.infoBox.id);
                } else {
                    this.liveInfo = '';
                    if (this.infoBox.status == 1 || this.infoBox.status == 2 || this.infoBox.status == 3) {
                        this.getInfo(this.$route.query.accountUnitId ? this.infoBox.roomId : this.infoBox.id);
                    }
                }
            }
            this.show_info = true;
        },
        // 展开信息
        getBedInfo(roomId) {
            GetBusinessOrderroomByroombed({ roomId: roomId }).done((res) => {
                this.bedInfoAll = res.data;
            });
        },
        // 展开信息
        getCooperInfo(id) {
            GetBusinessCooperOrderroomByroom({ roomId: id }).done((res) => {
                this.liveInfo = res.data;
                this.unixTime = dayjs(this.liveInfo.next_day).unix();
            });
        },
        // 展开信息
        getInfo(id) {
            GetBusinessOrderroomByroom(id).done((res) => {
                let start_time = new Date(res.data.order.startTime);
                var current_time = new Date();
                current_time.setTime(start_time.getTime() + 24 * 60 * 60 * 1000);
                var current_y = current_time.getFullYear() < 10 ? '0' + current_time.getFullYear() : current_time.getFullYear();
                var current_m = current_time.getMonth() + 1 < 10 ? '0' + (current_time.getMonth() + 1) : current_time.getMonth() + 1;
                var current_d = current_time.getDate() < 10 ? '0' + current_time.getDate() : current_time.getDate();
                res.data.next_day = current_y + '-' + current_m + '-' + current_d + ' ' + '08:00:00';
                this.liveInfo = res.data;
                this.unixTime = dayjs(this.liveInfo.next_day).unix();
            });
        },
        // 单位协议手动分房
        cooperHandleRoom() {
            this.$router.push({
                path: '/business-handle-room',
                query: {
                    hotelId: this.$route.query.hotelId
                }
            });
        },
        // 打扫完成
        cleanRoom() {
            if (this.infoBox.isRoomCheck != 0) {
                this.cleanRoomUp(this.infoBox.cleanOrderId);
            } else {
                this.$router.push({
                    path: '/stateCheck',
                    query: this.infoBox
                });
            }
        },
        // 房间清洁上线
        cleanRoomUp(id) {
            this.show_info = false;
            PostBusinessRoomCleanUp(id).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                this.findList();
            });
        },
        // 锁房
        lockRoom() {
            this.show_info = false;
            MessageBox.confirm('', {
                message: '是否锁房？',
                title: '提示',
                cancelButtonText: '否',
                confirmButtonText: '是'
            })
                // 成功的回调
                .then((action) => {
                    if (action == 'confirm') {
                        PostBusinessRoomLock(this.infoBox.id).done((res) => {
                            Toast({
                                message: res.msg,
                                duration: 900
                            });
                            this.findList();
                        });
                    }
                })
                // 取消的回调
                .catch((e) => {
                    console.log(e);
                });
        },
        // 维修，锁房-上线
        LiveRoom() {
            this.show_info = false;
            PostBusinessRoomOnline(this.infoBox.id).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                this.findList();
            });
        },
        // 更多操作
        getMoreList() {
            this.show_info = false;
            if (this.liveInfo.id) {
                GetBusinessOrderroom(this.liveInfo.id).done((res) => {
                    this.more = res.data;
                });
            }
            this.more_box = true;
        },
        // 跳到二维码页面
        goCode() {
            GetBusinessOrderroom(this.liveInfo.id).done((res) => {
                this.info = {};
                this.info.scaleName = res.data.roomScale.scaleName;
                this.info.roomSn = res.data.roomSn;
                this.info.orderId = res.data.orderId;
                this.$router.push({
                    path: '/quickLiveOnline',
                    query: {
                        fenfangInfo: this.info
                    }
                });
                localStorage.setItem('fenfangInfo', JSON.stringify(this.info));
            });
        },

        historyRecord() {
            if (this.$route.query.accountUnitId) {
                this.$router.push({
                    path: '/rentInfo_long',
                    query: {
                        orderRoomId: this.infoBox.id
                    }
                });
            } else {
                this.$router.push({
                    path: '/order',
                    query: {
                        roomId: this.infoBox.id,
                        roomSn: this.infoBox.roomSn
                    }
                });
                localStorage.setItem('history_unRoom', true);
            }
        },
        // 跳转到订单详情
        goDetail() {
            if (this.hotelType == '4') {
                this.$router.push({
                    path: '/apartMent',
                    query: {
                        roomId: this.infoBox.id
                    }
                });
            } else {
                if (this.liveInfo) {
                    if (this.infoBox.cooperId > 0 || this.infoBox.cleanCooper == 1) {
                        // 协议单位-cooperOrderRoomId
                        this.$router.push({
                            path: '/rentInfo_two',
                            query: {
                                roomId: this.infoBox.id,
                                cooperOrderRoomId: this.liveInfo.id,
                                cooperOrderId: this.liveInfo.cooperOrderId,
                                roomSn: this.infoBox.roomSn,
                                scaleName: this.infoBox.roomScale.scaleName
                            }
                        });
                    } else {
                        if (this.liveInfo.orderType == 3 || this.liveInfo.orderType == 4) {
                            sessionStorage.removeItem('orderRoomId');
                            this.$router.push({
                                name: 'RentInfo_long',
                                query: {
                                    orderRoomId: this.liveInfo.id
                                }
                            });
                        } else {
                            this.$router.push({
                                name: 'Details',
                                query: {
                                    orderRoomId: this.liveInfo.id
                                }
                            });
                        }
                    }
                }
            }
        },
        // 房费优惠
        btnback(more, num) {
            if (more.order.orderSource != 1) {
                document.getElementById('plat').style.backgroundColor = 'grey';
                document.getElementById('plat').style.color = '#fff';
            } else {
                var orderDetail = {
                    item: more,
                    num: num
                };
                this.$router.push({
                    name: 'DealLive',
                    query: orderDetail
                });
                localStorage.setItem('orderDetail', JSON.stringify(orderDetail));
            }
        },
        // 订单重新下发密码
        btnAgain() {
            this.more_box = false;
            PostBusinessOrderroomPasswordSend(this.more.id, { orderId: this.more.orderId }).done((res) => {
                MessageBox.alert('下发密码成功').then((action) => {
                    setTimeout('window.history.go(-1)', 500);
                });
            });
        },
        // 月租房费账单
        btnRoomFee() {
            this.$router.push({
                path: '/roomFee',
                query: {
                    id: this.more.id,
                    orderId: this.more.orderId
                }
            });
        },
        // 月租生活账单
        btnLifeFee() {
            this.$router.push({
                path: '/lifeFee',
                query: {
                    id: this.more.id
                }
            });
        },
        // 重新下发短信
        btnGetInfo() {
            this.more_box = false;
            this.password_show = true;
            if (this.more.order.phone) {
                this.pass_phone = this.more.sysUser == null ? this.more.order.phone : this.more.sysUser.phone;
            }
        },
        // 下发短信-修改密码-取消
        pass_can() {
            this.password_show = false;
        },
        // 下发短信-修改密码
        pass_ok() {
            if (!this.pass_phone || !/^1[3-9]\d{9}$/.test(this.pass_phone)) {
                Toast({
                    message: '请正确输入手机号',
                    duration: 900
                });
                this.pass_phone = '';
                return false;
            }
            let roomUser = this.more.sysUser == null ? this.more.order.roomUser : this.more.sysUser.realName;
            let params = {
                roomUser: roomUser,
                phone: this.pass_phone,
                orderId: this.more.orderId
            };
            PostBusinessOrderroomSendMessageAgain(this.more.id, params).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
            });
            this.password_show = false;
        },
        // 办理入住
        btnlive(item) {
            this.$router.push({
                name: 'DealLive',
                params: {
                    orderDetail: item
                }
            });
            localStorage.setItem('orderDetail', JSON.stringify(item));
        },
        // 取消订单
        btncancel(item) {
            this.show_info = false;
            MessageBox.confirm('', {
                message: '取消订单后，房费和押金将退还顾客，请慎重操作',
                title: '取消订单',
                cancelButtonText: '取消',
                confirmButtonText: '确定'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        let orderRoomId = item.id;
                        let data = {
                            orderId: item.orderId
                        };
                        PostBusinessOrderroomCancel(orderRoomId, data).done((res) => {
                            Toast({
                                message: res.msg,
                                duration: 900
                            });
                            this.findList();
                        });
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        },
        // 换房(未办理入住）
        btnchange(item) {
            this.$router.push({
                path: '/fdchange',
                query: {
                    orderDetail: item
                }
            });
            localStorage.setItem('orderDetail', JSON.stringify(item));
        },
        // 换房
        btnamend() {
            GetBusinessOrderroom(this.liveInfo.id).done((res) => {
                this.$router.push({
                    name: 'Amend',
                    query: {
                        item_amend: res.data
                    }
                });
                localStorage.setItem('item_amend', JSON.stringify(res.data));
            });
        },
        // 退房
        checkOut() {
            this.show_info = false;
            if (this.liveInfo.orderType == 3 || this.liveInfo.orderType == 4) {
                this.$router.push({
                    path: '/rentCount',
                    query: {
                        rentCount_info: this.liveInfo
                    }
                });
                localStorage.setItem('rentCount_info', JSON.stringify(this.liveInfo));
            } else {
                MessageBox.confirm('', {
                    message: '退房后，将不可恢复，请慎重操作',
                    title: '是否退房',
                    cancelButtonText: '取消',
                    confirmButtonText: '确定'
                })
                    .then((action) => {
                        if (action == 'confirm') {
                            let data = {
                                orderId: this.liveInfo.orderId,
                                orderRoomId: this.liveInfo.id
                            };
                            PostBusinessOrderroomLeave(data).done((res) => {
                                Toast({
                                    message: res.msg,
                                    duration: 900
                                });
                                this.findList();
                            });
                        }
                    })
                    .catch((e) => {
                        console.log(e);
                    });
            }
        },
        // 选中全部
        btnALL() {
            this.show_left = true;
        },
        // 选中租赁类型
        btnRent() {
            this.show_center = true;
        },
        // 选中房间状态
        btnStatus() {
            this.show_center_2 = true;
        },
        // 所有房型
        btnSort() {
            this.show_right = true;
        },
        // 选中(栋)
        choosed_all(buildNum) {
            this.leftName = buildNum;
            if (buildNum != '全部酒店') {
                this.param.buildNum = buildNum;
            } else {
                delete this.param.buildNum;
            }
            this.show_left = false;
            this.findList();
        },
        // 选中(租赁)
        choosed_rent(item) {
            if (item != '租赁类型') {
                this.center_1 = item.dictLabel;
                this.param.roomType = item.dictValue;
            } else {
                this.center_1 = item;
                delete this.param.roomType;
            }
            this.show_center = false;
            this.findList();
        },
        // 选中(状态)
        choosed_statu(item) {
            if (item != '房间状态') {
                this.center_2 = item.dictLabel;
                this.param.status = item.dictValue;
            } else {
                this.center_2 = item;
                delete this.param.status;
            }
            this.show_center_2 = false;
            this.findList();
        },
        // 选中(过滤)
        choosed_scale(item) {
            this.right = item;
            this.scaleName = item;
            this.show_right = false;
            this.findList();
        }
    }
};
</script>
